.TodoList{
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.467);
  color: white;
}
.TodoList h2{
  font-size: 2rem;
}
.TodoArrange{
  display: flex;
  flex-direction: column;
  text-align: center;
}
.Todo-input{
  padding: 10px;
  /* width: auto; */
  border-radius: 50px;
  border: none;
  background-color: rgba(128, 128, 128, 0.566);
}
.Todo-btn{
  padding: 10px;
  margin: 3px;
  border-radius: 50px;
  border: none;
  background-color:coral;
  color: white;
  cursor: pointer;

}
.Todo-btn:hover{
  transform: scale(1.25);
}
ul{
  list-style: none;
  padding: 0;
}
.TodoContent{
  text-align: center;
  background-color:rgb(241, 241, 135);
  background-size: cover;
  color: rgb(16, 14, 14);
  font-size: larger;
  border-radius: 5px;
  padding: 7px;
  margin: 5px;
  word-wrap: none;
  position: relative;
  /* width: 100%; */
  min-height: 5em;
  width: 300px;
}

.TodoContent textarea{
  text-align: center;
  width: 200px;
  position: sticky;
  height: 5rem;
  padding: 5px;
  background-color: rgb(241, 241, 135);
  color: rgb(58, 57, 57);
  border: none;
  margin: 5px;
  word-wrap: wrap;
  background-size: cover;
  outline: none;
}
.deletebtn{
  padding: 5px;
  border-radius: 50%;
  position: absolute;
  right: 0;
  top: 0;
  margin: 5px;
  background-color: rgb(227, 14, 14);
  color: white;
  cursor: pointer;
 }
 .editbtn{
  padding: 5px;
  border-radius: 50%;
  position: absolute;
  left:82%;
  top: 0;
  margin: 5px;
  background-color: white;
  cursor: pointer;
 }
 .completebtn{
  padding: 5px;
  border-radius: 50%;
  position: absolute;
  left:74%;
  top: 0;
  margin: 5px;
  background-color: white;
  cursor: pointer;
 }

.page{
  text-align: center;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cover{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  width: 30em;
  /* height: 35em; */
  margin: auto;
  -webkit-box-shadow: 1px 2px 6px 2px #919191; 
  box-shadow: 1px 2px 6px 2px #919191;
}
.bt{
  border: none;
}
.TodoStart input{
  padding: 10px;
  border: none;
  background-color: rgba(128, 128, 128, 0.566);
  border-radius: 50px;
  /* width: 150px; */
}
.TodoStart{
  margin: 20px;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  
}
.svg{
  margin-right: auto;
}